.s-searching-results {
    &__top {
        display: flex;
        margin-bottom: rem(25);
        flex-direction: column;
        gap: rem(10);

        @media (min-width: 768px) {
            align-items: center;
            justify-content: space-between;
            margin-bottom: rem(40);
            flex-direction: row;
            gap: rem(54);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(50);
        }

        @media (min-width: 1600px) {
            align-items: flex-end;
            margin-bottom: rem(60);
        }
    }

    &__title {
        font-size: rem(35);
        font-weight: 500;
        line-height: rem(33);
        color: #4e5056;
        letter-spacing: -0.03em;

        @media (min-width: 768px) {
            font-size: rem(40);
            line-height: rem(38);
        }

        @media (min-width: 1024px) {
            font-size: rem(55);
            line-height: rem(52);
        }

        @media (min-width: 1600px) {
            display: flex;
            align-items: center;
            font-size: rem(65);
            line-height: rem(65);
            gap: rem(60);
        }

        @media (min-width: 1920px) {
            font-size: rem(80);
            line-height: rem(80);
            gap: rem(42);
        }

        span {
            color: white;

            @media (min-width: 768px) {
                display: block;
            }
        }
    }

    &__found {
        font-size: rem(15);
        line-height: rem(22);
        color: #656669;

        @media (min-width: 1024px) {
            font-size: rem(17);
            line-height: rem(26);
        }

        @media (min-width: 1600px) {
            font-size: rem(19);
            line-height: rem(28);
        }

        @media (min-width: 1920px) {
            font-size: rem(21);
            line-height: rem(32);
        }
    }

    &__main {
        @media (min-width: 768px) {
            display: flex;
            gap: rem(34);
        }

        @media (min-width: 1024px) {
            gap: rem(46);
        }

        @media (min-width: 1600px) {
            gap: rem(85);
        }

        @media (min-width: 1920px) {
            gap: rem(91);
        }
    }

    &__filter {
        @include tr("all");
        margin-bottom: rem(28);
        flex: none;
        height: 0;
        overflow: hidden;

        @media (min-width: 768px) {
            width: rem(180);
            height: auto;
        }

        @media (min-width: 1024px) {
            width: rem(227);
        }

        @media (min-width: 1600px) {
            width: rem(285);
        }

        @media (min-width: 1920px) {
            width: rem(399);
        }

        &.open {
            height: auto;
        }

        &-title {
            display: none;
            margin-bottom: rem(18);
            font-size: rem(15);
            line-height: rem(22);
            color: #ceced0;

            @media (min-width: 767px) {
                display: block;
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(15);
                font-size: rem(19);
                line-height: rem(28);
            }

            @media (min-width: 1600px) {
                margin-bottom: rem(35);
                font-size: rem(22);
                line-height: rem(33);
            }
        }
    }

    &__tabs {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        gap: rem(5);

        @media (min-width: 768px) {
            gap: rem(10);
        }
    }

    &__tab {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        height: rem(45);
        padding-left: rem(120);
        font-size: rem(15);
        line-height: rem(22);
        transition: 0.5s;
        border-left: rem(2) solid transparent;

        @media (min-width: 768px) {
            height: rem(42);
            padding: 0 rem(16);
        }

        @media (min-width: 1024px) {
            font-size: rem(19);
            line-height: rem(28);
        }

        @media (min-width: 1600px) {
            font-size: rem(22);
            line-height: rem(33);
        }

        &.is-active,
        &:hover {
            background: #111219;
            border-color: #d50032;
        }
    }

    &__contents {
        width: auto;
    }

    &__content {
        display: none;
        animation: show 0.4s ease-in-out;

        &.is-active {
            display: block;
        }
    }

    &__items {
        display: flex;
        margin-bottom: rem(40);
        flex-direction: column;
        gap: rem(10);

        @media (min-width: 1024px) {
            margin-bottom: rem(70);
            gap: rem(15);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(104);
            gap: rem(20);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(90);
        }
    }

    &__item {
        padding: rem(20) rem(36);
        border: rem(2) solid;
        border-image-slice: 1;
        border-width: rem(2);
        border-image-source: linear-gradient(
            124.81deg,
            #000000 -36.41%,
            #656669 117.15%
        );

        @media (min-width: 768px) {
            padding: rem(20) rem(30);
        }

        @media (min-width: 1024px) {
            padding: rem(30) rem(40);
        }

        @media (min-width: 1600px) {
            padding: rem(37) rem(68);
        }

        @media (min-width: 1920px) {
            padding: rem(35) rem(58);
        }

        &-path {
            margin-bottom: rem(12);
            font-size: rem(10);
            line-height: rem(13);
            color: #4e5056;

            @media (min-width: 768px) {
                margin-bottom: rem(10);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(18);
                font-size: rem(12);
                line-height: rem(16);
            }

            @media (min-width: 1600px) {
                margin-bottom: rem(20);
                font-size: rem(15);
                line-height: rem(19);
            }
        }

        &-title {
            font-size: rem(19);
            font-weight: 600;
            line-height: rem(23);
            color: #ceced0;
            margin-bottom: rem(8);

            @media (min-width: 768px) {
                margin-bottom: rem(5);
            }

            @media (min-width: 1024px) {
                font-size: rem(21);
                line-height: rem(25);
                margin-bottom: rem(10);
            }

            @media (min-width: 1600px) {
                font-size: rem(32);
                line-height: rem(42);
                margin-bottom: rem(15);
            }

            @media (min-width: 1920px) {
                line-height: rem(45);
            }
        }

        &-text {
            font-size: rem(13);
            line-height: rem(16);
            color: #ceced0;

            @media (min-width: 1024px) {
                font-size: rem(17);
                line-height: rem(20);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
                line-height: rem(23);
            }
        }
    }

    &__navigation {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: rem(60);
    }

    &__paginations {
        display: flex;
        align-items: center;
        list-style: none;
        gap: rem(25);

        @media (min-width: 768px) {
            gap: rem(40);
        }

        @media (min-width: 1024px) {
            gap: rem(60);
        }

        @media (min-width: 1600px) {
            gap: rem(70);
        }
    }

    &__pagination {
        &-item {
            font-size: rem(15);
            line-height: rem(22);
            color: #4e5056;
            transition: 0.5s;

            @media (min-width: 1024px) {
                font-size: rem(17);
                line-height: rem(26);
            }

            @media (min-width: 1600px) {
                font-size: rem(22);
                line-height: rem(33);
            }

            &:hover {
                color: #d91745;
            }

            &.is-current {
                color: white;
            }
        }
    }

    &__back-link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: rem(13);
        font-weight: 300;
        line-height: rem(16);
        color: #656669;
        transition: 0.5s;
        gap: rem(20);

        @media (min-width: 1024px) {
            font-size: rem(15);
            line-height: rem(18);
            gap: rem(20);
        }

        &:hover {
            color: #d91745;

            svg {
                path {
                    stroke: #d91745;
                }
            }
        }
    }

    &__dropdown {
        display: none;
        color: $grey_light;
        background: #000;
        padding: 11px 0;

        @media screen and (max-width: 767px) {
            display: block;
        }

        &-arrow {
            @include tr("all");
        }

        &.open {
            .s-searching-results {
                &__dropdown {
                    &-arrow {
                        transform: rotate(180deg);
                    }
                }
            }
        }

        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            max-width: 111px;
            margin-left: rem(120);
        }
    }

    &-empty {
        padding-bottom: 62px;

        .s-searching-results {
            &__title {
                display: block;
                font-size: 70px;

                @media screen and (max-width: 1699px) {
                    font-size: 65px;
                }

                @media screen and (max-width: 1099px) {
                    font-size: 55px;
                    margin-bottom: 15px;
                }

                @media screen and (max-width: 991px) {
                    font-size: rem(40);
                }

                @media screen and (max-width: 600px) {
                    font-size: 35px;
                    margin-bottom: 10px;
                }

                span {
                    display: inline-block;
                }
            }

            &__top {
                align-items: center;
                margin-bottom: rem(87);

                @media screen and (max-width: 1699px) {
                    margin-bottom: 90px;
                }

                @media screen and (max-width: 1099px) {
                    gap: 0;
                    flex-direction: column;
                    align-items: flex-start;
                    margin-bottom: 20px;
                }

                @media screen and (max-width: 991px) {
                    margin-bottom: 30px;
                }

                @media screen and (max-width: 600px) {
                    margin-bottom: 35px;
                }
            }

            &__found {
                white-space: nowrap;

                @media screen and (max-width: 1099px) {
                    margin-left: auto;
                }

                @media screen and (max-width: 991px) {
                    margin-left: 0;
                }
            }

            &__content {
                display: flex;

                @media screen and (max-width: 991px) {
                    flex-direction: column;
                }

                &-popular {
                    display: block;
                    width: 100%;
                }
            }

            &__item {
                margin-bottom: rem(60);
                padding: 60px 0;

                @media screen and (max-width: 1699px) {
                    padding: 37px 38px;
                }

                @media screen and (max-width: 991px) {
                    padding: 35px 47px;
                    margin-bottom: 30px;
                }

                @media screen and (max-width: 600px) {
                    padding: 25px 30px;
                    margin-bottom: 20px;
                }

                &-wrapper {
                    max-width: 765px;
                    margin: 0 auto;
                }

                &-links {
                    display: flex;
                    width: 100%;
                    align-items: center;
                    justify-content: space-between;
                    flex-wrap: wrap;
                }

                &-link {
                    display: flex;
                    align-items: center;
                    width: 33.3333%;
                    margin-top: 24px;

                    @media screen and (max-width: 1699px) {
                        width: 50%;
                    }

                    @media screen and (max-width: 991px) {
                        width: 33.3333%;
                    }

                    @media screen and (max-width: 600px) {
                        width: 50%;
                        margin-top: 15px;
                    }

                    &:hover {
                        color: #656669;
                    }

                    &--arrow {
                        margin-left: 31px;

                        @media screen and (max-width: 1099px) {
                            margin-left: 30px;
                        }

                        @media screen and (max-width: 600px) {
                            margin-left: 20px;
                        }
                    }

                    &--text {
                        font-size: 21px;
                        font-weight: 300;
                        line-height: 1;

                        @media screen and (max-width: 1099px) {
                            font-size: 17px;
                        }

                        @media screen and (max-width: 600px) {
                            font-size: 19px;
                        }

                        @media screen and (max-width: 600px) {
                            font-size: 13px;
                        }
                    }
                }

                &-title {
                    font-size: 28px;
                    font-weight: 300;

                    @media screen and (max-width: 1099px) {
                        font-size: 25px;
                    }

                    @media screen and (max-width: 600px) {
                        font-size: 19px;
                    }
                }
            }

            &__back {
                &-link {
                    max-width: fit-content;
                    margin-left: auto;
                }
            }

            &__sentences {
                min-width: 421px;
                margin-right: 81px;

                @media screen and (max-width: 1099px) {
                    min-width: 377px;
                    margin-right: rem(50);
                }

                @media screen and (max-width: 991px) {
                    padding-left: rem(47);
                    margin-right: 0;
                }

                @media screen and (max-width: 600px) {
                    padding-left: 0;
                    margin-bottom: rem(36);
                }

                &-title {
                    position: relative;
                    font-size: 28px;
                    line-height: 120%;
                    font-weight: 600;
                    margin-bottom: 30px;

                    @media screen and (max-width: 1099px) {
                        font-size: 25px;
                    }

                    @media screen and (max-width: 600px) {
                        font-size: 19px;
                    }

                    &:after {
                        content: "";
                        display: block;
                        width: 290px;
                        height: 1px;
                        background: $red;

                        @media screen and (max-width: 1099px) {
                            width: 250px;
                        }

                        @media screen and (max-width: 600px) {
                            width: 216px;
                        }
                    }
                }

                &-text {
                    p {
                        font-size: 19px;
                        line-height: 120%;
                        margin-bottom: 27px;

                        @media screen and (max-width: 1099px) {
                            font-size: 17px;
                        }

                        @media screen and (max-width: 600px) {
                            font-size: 15px;
                            margin-bottom: rem(18);
                        }
                    }
                }
            }
        }
    }
}
