@import "helpers/index";
@import "varibles/index";
@import "../blocks/modules/search/s_past_searches/s_past_searches";
@import "../blocks/modules/search/s_searching_results/s_searching_results";


.searching_results-page {
    position: relative;

    .main {
        position: relative;

        &:after {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: url('.././img/s-search/searching-result-bg.png');
            content: '';
            background-repeat: no-repeat;
            background-size: cover;
            background-position: top;
        }
    }
}