.s-past-searches {
    margin: rem(10) 0 rem(25);
    padding: rem(6) 0;
    background: rgba(0, 0, 0, 0.2);

    @media (min-width: 768px) {
        margin: rem(15) 0 rem(25);
        padding: rem(10) 0;
    }

    @media (min-width: 1024px) {
        margin: rem(15) 0 rem(35);
        padding: rem(15) 0;
    }

    @media (min-width: 1600px) {
        margin: rem(18) 0 rem(40);
        padding: rem(24) 0;
    }

    &__wrap {
        display: flex;
        align-items: center;
        width: 100%;
        gap: rem(16);

        @media (min-width: 1024px) {
            gap: rem(36);
        }

        @media (min-width: 1600px) {
            gap: rem(16);
        }

        @media (min-width: 1920px) {
            gap: rem(44);
        }
    }

    &__title {
        width: rem(70);
        font-size: rem(10);
        font-weight: 300;
        line-height: rem(10);
        color: $grey_light;
        flex: none;
        letter-spacing: -0.03em;

        @media (min-width: 768px) {
            width: auto;
            font-size: rem(12);
            line-height: rem(12);
        }

        @media (min-width: 1024px) {
            width: rem(160);
            font-size: rem(15);
            line-height: rem(15);
        }

        @media (min-width: 1600px) {
            width: auto;
            font-size: rem(17);
            line-height: rem(17);
        }

        @media (min-width: 1920px) {
            font-size: rem(19);
            line-height: rem(19);
        }
    }

    &__list {
        overflow: -moz-scrollbars-none;
        display: flex;
        align-items: center;
        overflow-x: scroll;
        gap: rem(12);

        @media (min-width: 768px) {
            gap: rem(15);
        }

        @media (min-width: 1600px) {
            gap: rem(20);
        }

        @media (min-width: 1920px) {
            gap: rem(12);
        }

        &::-webkit-scrollbar {
            display: none;
        }
    }

    &__item {
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        width: rem(127);
        height: rem(44);
        padding: 0 rem(10);
        font-size: rem(10);
        line-height: rem(12);
        color: $grey_light_more_2;
        border: rem(2) solid #000000;
        border-radius: rem(5);
        flex: none;
        background-size: cover;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;

        @media (min-width: 768px) {
            width: rem(184);
            height: rem(75);
            padding: 0 rem(35);
            font-size: rem(12);
            line-height: rem(14);
        }

        @media (min-width: 1600px) {
            width: rem(265);
            padding: 0 rem(48);
        }
    }
}
